import React from 'react';
import Home from './Home';
import AfterLayout from '../../components/Layout';
import BeforeLogin from '../../components/Layout/BeforeLogin';

async function action({store}) {
  const userLoginCheck = store.getState().userLogin.userLogin;
  const title = 'Home';
  const Layout = userLoginCheck ? AfterLayout : BeforeLogin;
  return {
    title,
    chunks: ['home'],
    component: (
      <Layout>
        <Home />
      </Layout>
    ),
  };
}

export default action;
